import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
        });
    }

    clickHandle(x,y){
        let newArray = this.props.gridArray;

        newArray[x][y] = 0;

        this.props.mapActions.changedMapSource(newArray);
    }

    render(){
        let num = this.props.serialNumber;

        if(num === 0){
            return(
                <div className="cell" onClick={this.clickHandle.bind(this, this.props.x, this.props.y)}>
                    <img
                        src="img/cakeHalfAlt.png"
                        width={50}
                        height={50}
                    />
                </div>
            );
        }
        return(
            <div className="cell" onClick={this.clickHandle.bind(this, this.props.x, this.props.y)}>
                {/*{num}*/}
            </div>
        );
    }
}